<template>
  <button type="button" aria-label="Убрать один товар"
    @click.prevent="decrement">
    <svg width="12" height="12" fill="currentColor">
    <use xlink:href="#icon-minus"></use>
     </svg>
  </button>

</template>

<script>
/* eslint-disable */
export default {
  props: ['productAmount'],
  methods: {
    decrement() {
      this.$emit('update:productAmount', this.productAmount -= 1);
    },
  },
};
</script>
